<template>
  <div class="book_page" id="book_page">
    <div class="pre_arrow_part" @click="autoLeftToRight"></div>
    <div class="body_part">
      <div class="books">
        <div class="page">
          <div class="page_left"  @click="autoLeftToRight">
            <img v-if="pageRotateSrc.L1" class="img_left" :src="pageLeftSrc.L1"
                 :style="'transform: translateX('+floatLeftValue * animeFloatL1+'rem);'"/>
            <img v-if="pageRotateSrc.L2" class="img_left" :src="pageLeftSrc.L2"
                 :style="'transform: translateX('+floatLeftValue * animeFloatL2+'rem);'"/>
            <img v-if="pageRotateSrc.L3" class="img_left" :src="pageLeftSrc.L3"
                 :style="'transform: translateX('+floatLeftValue * animeFloatL3+'rem);'"/>
            <img v-if="pageRotateSrc.L4" class="img_left" :src="pageLeftSrc.L4"
                 :style="'transform: translateX('+floatLeftValue * animeFloatL4+'rem);'"/>
          </div>
<!--          TODO: 这里为了避免左翻页会产生闪动情况，计划分别使用两个翻页做效果-->
          <div :class="pageRotate >= -90 ? 'page_rotate':'page_rotate_left'" :style="'transform: rotateY(' + pageRotate + 'deg)'"  @click="autoRightToLeft">
            <img class="page_shadow" src="/static/book/page_shadow.png"
                 :style="pageRotate >= -90 ? 'opacity:'+(-pageRotate)/90*shadowMax : 'transform: scaleX(1); opacity:'+(180+pageRotate)/90*shadowMax"/>
            <img v-if="pageRotateSrc.L1" class="img_right"
                 :src="pageRotateSrc.L1"
                 :style="'transform: translateX('+(pageRotate >= -90 ? floatLeftValue * animeFloatL1+'rem);' : -floatRightValue * animeFloatL1+'rem) scaleX(-1);')"/>
            <img v-if="pageRotateSrc.L2" class="img_right"
                 :src="pageRotateSrc.L2"
                 :style="'transform: translateX('+(pageRotate >= -90 ? floatLeftValue * animeFloatL2+'rem);' : -floatRightValue * animeFloatL2+'rem) scaleX(-1);')"/>
            <img v-if="pageRotateSrc.L3" class="img_right"
                 :src="pageRotateSrc.L3"
                 :style="'transform: translateX('+(pageRotate >= -90 ? floatLeftValue * animeFloatL3+'rem);' : -floatRightValue * animeFloatL3+'rem) scaleX(-1);')"/>
            <img v-if="pageRotateSrc.L4" class="img_right"
                 :src="pageRotateSrc.L4"
                 :style="'transform: translateX('+(pageRotate >= -90 ? floatLeftValue * animeFloatL4+'rem);' : -floatRightValue * animeFloatL4+'rem) scaleX(-1);')"/>
          </div>
          <div class="page_right"  @click="autoRightToLeft">
            <img v-if="pageRotateSrc.L1" class="img_right" :src="pageRightSrc.L1"
                 :style="'transform: translateX('+floatRightValue * animeFloatL1+'rem);'"/>
            <img v-if="pageRotateSrc.L2" class="img_right" :src="pageRightSrc.L2"
                 :style="'transform: translateX('+floatRightValue * animeFloatL2+'rem);'"/>
            <img v-if="pageRotateSrc.L3" class="img_right" :src="pageRightSrc.L3"
                 :style="'transform: translateX('+floatRightValue * animeFloatL3+'rem);'"/>
            <img v-if="pageRotateSrc.L4" class="img_right" :src="pageRightSrc.L4"
                 :style="'transform: translateX('+floatRightValue * animeFloatL4+'rem);'"/>
          </div>
        </div>
      </div>
    </div>
    <div class="next_arrow_part" @click="autoRightToLeft"></div>
    <LangTransfer></LangTransfer>
    <Menu></Menu>
  </div>
</template>

<style scoped lang="css">
::-webkit-scrollbar {
  display: none;
}

div.book_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: row;
  overflow: hidden;
}

div.pre_arrow_part {
  width: 200rem;
  height: 100%;
  margin-left: 100rem;
  /*background: #000000;*/
}

div.next_arrow_part {
  width: 200rem;
  height: 100%;
  margin-right: 100rem;
  /*background: #000000;*/
}

div.body_part {
  width: 1320rem;
  height: 100%;
  display: flex;
  align-items: center;
  justify-items: center;
}

div.books {
  width: 1320rem;
  height: 752.6rem;
}

div.page {
  position: absolute;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-items: center;
  width: 1320rem;
  height: 752.6rem;
  perspective: 2500px;
  transform-style: preserve-3d;
}

div.page_left {
  transform-origin: right;
  position: absolute;
  width: 660rem;
  height: 752.6rem;
  overflow: hidden;
  cursor: url("/static/book/pre_arrow.png"), pointer;
}

div.page_rotate {
  transform-origin: left;
  position: absolute;
  right: 0;
  width: 660rem;
  height: 752.6rem;
  overflow: hidden;
  z-index: 1;
  cursor: url("/static/book/next_arrow.png"), pointer;
}

div.page_rotate_left {
  position: absolute;
  left: 0;
  width: 1320rem;
  height: 752.6rem;
  cursor: url("/static/book/pre_arrow.png"), pointer;
}

img.page_shadow {
  position: absolute;
  width: 1320rem;
  height: 752.6rem;
  z-index: 1;
}

div.page_right {
  transform-origin: left;
  position: absolute;
  right: 0;
  width: 660rem;
  height: 752.6rem;
  overflow: hidden;
  cursor: url("/static/book/next_arrow.png"), pointer;
}

img.img_left {
  position: absolute;
  left: 0;
  width: 1320rem;
  height: 752.6rem;
}

img.img_right {
  position: absolute;
  right: 0;
  width: 1320rem;
  height: 752.6rem;
}
</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer";
import Footer from "../../components/footer/Footer";
// when flipAnimeTask is null means anime finished
let flipAnimeTask

//export default
export default {
  name: "Stories",
  data() {
    return {
      global: global,
      pageRotate: 0,
      curPageIdx: 0,
      tarPageIdx: 0,
      pageLeftSrc: null,
      pageRotateSrc: null,
      pageRightSrc: null,
      pageList: [], //页面布局信息
      totalPageNum: 8,//默认多少页书||增加或删除页面，需要改变这个页数的值||
      shadowMax: 0.2,//页面翻转阴影最大值
      animeMaxLength: 660,//图层浮动的最大范围
      animeFloatL1: 0,//L1层的浮动比例
      animeFloatL2: 0.2,//L2层的浮动比例
      animeFloatL3: 0.8,//L3层的浮动比例
      animeFloatL4: 0.4,//L4层的浮动比例
      floatLeftValue: 0,//左半部分浮动值
      floatRightValue: 0,//右半部分浮动值
    };
  },
  components: {
    Menu,
    LangTransfer,
    Footer,
  },
  beforeCreate() {
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {
    this.init()
  },
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    this.refresh_root_size();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
  },
  watch: {
    // watch the size change to refresh
    "global.width": function () {
      this.refresh_root_size();
    },
    // Listen the page rotate event and then change the img and the show mode
    pageRotate: function (pageRotate, oldPageRotate) {
      // from left to right
      if (oldPageRotate > pageRotate && oldPageRotate - pageRotate < 179) {
        if (pageRotate === -180) {
          this.curPageIdx += 1
          this.pageRotate = 0
        }
      }
      // form right to left
      else if (oldPageRotate < pageRotate && pageRotate - oldPageRotate < 179) {
        if (oldPageRotate === -180) {
          this.curPageIdx -= 1
        }
      }
      this.updateImg()
      this.updateMove()
    },
  },
  methods: {
    refresh_root_size() {
      let static_pixel = 0;
      if (global.width < static_pixel) {
        document.documentElement.style.fontSize = static_pixel / 1920 + "px"
        return
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px"
    },
    init() {
      let that = this
      // wait for cover flip
      setTimeout(()=>{
        that.autoRightToLeft();
      }, 500)
      // initial the page list
      for (let i = 1; i <= this.totalPageNum; i++) {
        this.pageList.push({
          L1: "/static/book/" + i + "/1.png",
          L2: "/static/book/" + i + "/2.png",
          L3: "/static/book/" + i + "/3.png",
          L4: "/static/book/" + i + "/4.png",
        })
      }
      this.updateImg()
    },
    prepareRightToLeft() {
      if (this.pageRotate === -180)
        this.pageRotate = 0
      this.pageRightSrc = this.pageList[this.curPageIdx + 1]
    },
    prepareLeftToRight() {
      if (this.pageRotate === 0)
        this.pageRotate = -180
      this.pageRightSrc = this.pageList[this.curPageIdx]
    },
    updateImg() {
      this.pageLeftSrc = this.pageList[this.curPageIdx]
      if (this.curPageIdx + 1 !== this.pageList.length) {
        this.pageRightSrc = this.pageList[this.curPageIdx + 1]
        this.pageRotateSrc = this.pageRotate >= -90 ? this.pageList[this.curPageIdx] : this.pageList[this.curPageIdx + 1]
      }
    },
    updateMove() {
      this.floatLeftValue = this.pageRotate / 180 * this.animeMaxLength
      this.floatRightValue = this.floatLeftValue + this.animeMaxLength
    },
    autoLeftToRight() {
      if (this.curPageIdx === 0 || this.tarPageIdx === 0 || flipAnimeTask != null)
        return
      this.tarPageIdx -= 1;
      this.prepareLeftToRight()
      flipAnimeTask = this.flipAnime(this.pageRotate, 0)
    },
    autoRightToLeft() {
      if (this.curPageIdx === this.pageList.length - 1 || this.tarPageIdx === this.pageList.length - 1 || flipAnimeTask != null)
        return
      this.tarPageIdx += 1;
      this.prepareRightToLeft()
      flipAnimeTask = this.flipAnime(this.pageRotate, -180)
    },
    flipAnime(start, end, time = 1000) {
      return anime({
        targets: this,
        pageRotate: [start, end],
        duration: time,
        easing: "linear",
        complete() {
          flipAnimeTask = null
        }
      });
    }

  },
};
</script>



